/**
 * Contenu principal page produits (Client Component) - OPTIMISÉ
 */
'use client';

import { ProductCardSkeleton } from '@/components/LoadingSkeleton';
import { useProducts } from '@/lib/hooks/useProducts';
import { API_URL } from '@/lib/utils/api';
import { useRouter, useSearchParams } from 'next/navigation';
import { useEffect, useMemo, useState } from 'react';
import { AccessibleFilterSidebar } from './AccessibleFilterSidebar';
import { OptimizedSearchBar } from './OptimizedSearchBar';
import { Product } from './ProductCard';
import { ProductGrid } from './ProductGrid';

export function ProductsPageContent() {
  const router = useRouter();
  const searchParams = useSearchParams();
  const [categories, setCategories] = useState<string[]>([]);

  // Récupérer les paramètres URL
  const filters = useMemo(() => ({
    search: searchParams.get('search') || '',
    category: searchParams.get('category') || '',
    price_min: searchParams.get('price_min') || '',
    price_max: searchParams.get('price_max') || '',
    stock: searchParams.get('stock') || '',
    stock_status: searchParams.get('stock_status') || '',
    tags: searchParams.get('tags') || '',
    sort: searchParams.get('sort') || 'created_at_desc',
    page: parseInt(searchParams.get('page') || '1'),
    limit: 20,
  }), [searchParams]);

  // Utiliser le hook optimisé avec cache
  const { products, isLoading, error, pagination, revalidate } = useProducts(filters);

  // Charger catégories (une seule fois)
  useEffect(() => {
    const fetchCategories = async () => {
      try {
        const response = await fetch(`${API_URL}/products?limit=1000`);
        const data = await response.json();
        const uniqueCategories = [
          ...new Set(
            (data.products || [])
              .map((p: Product) => p.category)
              .filter((c: string | null) => c !== null)
          ),
        ] as string[];
        setCategories(uniqueCategories);
      } catch (error) {
        if (process.env.NODE_ENV === 'development') {
          console.error('Erreur chargement catégories:', error);
        }
      }
    };

    fetchCategories();
  }, []);

  if (error) {
    return (
      <div className="text-center py-12">
        <p className="text-red-600 text-lg mb-4">{error}</p>
        <button
          onClick={revalidate}
          className="px-6 py-3 bg-primary-600 text-white font-semibold rounded-lg hover:bg-primary-700 transition-colors"
        >
          Réessayer
        </button>
      </div>
    );
  }

  return (
    <div className="flex flex-col lg:flex-row gap-8">
      {/* Sidebar filtres */}
      <div className="lg:w-64 flex-shrink-0">
        <AccessibleFilterSidebar categories={categories} />
      </div>

      {/* Contenu principal */}
      <div className="flex-grow">
        {/* Header avec recherche et tri */}
        <div className="mb-6 space-y-4">
          <OptimizedSearchBar />

          {/* Tri */}
          <div className="flex items-center justify-between">
            <p className="text-sm text-gray-600 dark:text-gray-300">
              {pagination.total} produit{pagination.total > 1 ? 's' : ''} trouvé
              {pagination.total > 1 ? 's' : ''}
            </p>
            <select
              value={filters.sort}
              onChange={(e) => {
                const params = new URLSearchParams(searchParams.toString());
                params.set('sort', e.target.value);
                router.push(`/products?${params.toString()}`);
              }}
              className="px-4 py-2 border border-gray-300 dark:border-gray-700 rounded-lg focus:outline-none focus:ring-2 focus:ring-primary-500 bg-white dark:bg-gray-900 text-gray-900 dark:text-gray-100"
              aria-label="Trier les produits"
            >
              <option value="created_at_desc">Plus récents</option>
              <option value="price_asc">Prix croissant</option>
              <option value="price_desc">Prix décroissant</option>
            </select>
          </div>
        </div>

        {/* Grille produits */}
        {isLoading ? (
          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">
            {Array.from({ length: 8 }).map((_, i) => (
              <ProductCardSkeleton key={i} />
            ))}
          </div>
        ) : (
          <ProductGrid products={products} isLoading={false} />
        )}

        {/* Pagination */}
        {pagination.totalPages > 1 && (
          <div className="mt-8 flex justify-center gap-2" role="navigation" aria-label="Pagination">
            {[...Array(pagination.totalPages)].map((_, i) => {
              const pageNum = i + 1;
              const params = new URLSearchParams(searchParams.toString());
              params.set('page', pageNum.toString());

              return (
                <a
                  key={pageNum}
                  href={`/products?${params.toString()}`}
                  className={`px-4 py-2 rounded-lg transition-colors ${
                    filters.page === pageNum
                      ? 'bg-primary-600 text-white'
                      : 'bg-white dark:bg-gray-900 text-gray-700 dark:text-gray-200 hover:bg-primary-50 dark:hover:bg-gray-800 border border-gray-200 dark:border-gray-800'
                  }`}
                  aria-label={`Page ${pageNum}`}
                  aria-current={filters.page === pageNum ? 'page' : undefined}
                >
                  {pageNum}
                </a>
              );
            })}
          </div>
        )}
      </div>
    </div>
  );
}
