/**
 * ProductCard optimisé avec lazy loading et prefetch
 */
'use client';

import { AddToCartButton } from '@/components/AddToCartButton';
import Image from 'next/image';
import Link from 'next/link';
import { useEffect, useRef, useState } from 'react';
import { Product } from './ProductCard';

interface OptimizedProductCardProps {
  product: Product;
  priority?: boolean;
}

export function OptimizedProductCard({ product, priority = false }: OptimizedProductCardProps) {
  const [isInView, setIsInView] = useState(priority);
  const cardRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (priority || isInView) return;

    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsInView(true);
          observer.disconnect();
        }
      },
      { rootMargin: '100px' }
    );

    if (cardRef.current) {
      observer.observe(cardRef.current);
    }

    return () => observer.disconnect();
  }, [priority, isInView]);

  return (
    <div
      ref={cardRef}
      className="group hover:shadow-xl transition-all duration-300 bg-white rounded-xl overflow-hidden h-full flex flex-col"
    >
      {/* Image avec lazy loading */}
      <Link
        href={`/products/${product.id}`}
        className="relative w-full h-80 bg-gradient-to-br from-primary-50 to-accent-50"
        onMouseEnter={() => {
          // Prefetch page produit au survol
          if (typeof window !== 'undefined') {
            const link = document.createElement('link');
            link.rel = 'prefetch';
            link.href = `/products/${product.id}`;
            document.head.appendChild(link);
          }
        }}
      >
        {isInView ? (
          <Image
            src={product.images[0] || '/placeholder-product.jpg'}
            alt={product.title}
            fill
            className="object-cover group-hover:scale-105 transition-transform duration-500"
            sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
            placeholder="blur"
            blurDataURL="data:image/webp;base64,UklGRiIAAABXRUJQVlA4IBYAAAAwAQCdASoBAAEADsD+JaQAA3AAAAAA"
            quality={85}
            loading={priority ? 'eager' : 'lazy'}
            priority={priority}
          />
        ) : (
          <div className="w-full h-full bg-gray-200 animate-pulse" />
        )}
        {product.stock === 0 && (
          <div className="absolute top-3 right-3 bg-red-500 text-white px-2 py-1 rounded-full text-xs font-bold">
            Rupture
          </div>
        )}
      </Link>

      {/* Content */}
      <div className="p-6 flex flex-col flex-grow">
        <h3 className="font-semibold text-lg line-clamp-2 leading-tight mb-2 group-hover:text-primary-600 transition-colors">
          {product.title}
        </h3>
        {product.description && (
          <p className="text-sm text-gray-600 line-clamp-2 mb-4 flex-grow">
            {product.description}
          </p>
        )}
        <div className="flex items-center justify-between mb-4">
          <span className="text-2xl font-bold text-emerald-600">
            {product.price.toFixed(2)}€
          </span>
          <span className="text-sm text-gray-500">
            Stock: {product.stock}
          </span>
        </div>
        <AddToCartButton product={product} className="w-full" />
      </div>
    </div>
  );
}
