/**
 * Hook pour récupérer les produits avec cache SWR
 */
'use client';

import { API_URL } from '@/lib/utils/api';
import useSWR from 'swr';

export interface Product {
  id: string;
  title: string;
  description: string | null;
  price: number;
  category: string | null;
  stock: number;
  images: string[];
  tags: string[];
  created_at: string;
  updated_at: string;
  is_deleted: boolean;
}

interface ProductsResponse {
  products: Product[];
  pagination?: {
    page: number;
    limit: number;
    total: number;
    totalPages: number;
  };
}

const fetcher = async (url: string): Promise<ProductsResponse> => {
  const response = await fetch(url);
  if (!response.ok) {
    throw new Error('Erreur lors du chargement des produits');
  }
  const data = await response.json();
  // Gérer les deux formats de réponse possibles
  if (Array.isArray(data)) {
    return { products: data };
  }
  return data;
};

export function useProducts(options?: {
  limit?: number;
  sort?: string;
  category?: string;
  search?: string;
}) {
  const params = new URLSearchParams();
  if (options?.limit) params.append('limit', options.limit.toString());
  if (options?.sort) params.append('sort', options.sort);
  if (options?.category) params.append('category', options.category);
  if (options?.search) params.append('search', options.search);

  const endpoint = params.toString()
    ? `${API_URL}/products?${params.toString()}`
    : `${API_URL}/products`;

  const { data, error, isLoading, mutate } = useSWR<ProductsResponse>(
    endpoint,
    fetcher,
    {
      revalidateOnFocus: false, // Ne pas revalider automatiquement au focus
      revalidateOnReconnect: true, // Revalider après reconnexion
      dedupingInterval: 5000, // Déduplication des requêtes pendant 5s
      refreshInterval: 0, // Pas de refresh automatique
    }
  );

  return {
    products: data?.products || [],
    pagination: data?.pagination,
    isLoading,
    isError: error,
    error: error?.message,
    mutate, // Permet de revalider manuellement
  };
}

export function useFeaturedProducts(limit: number = 8) {
  return useProducts({ limit, sort: 'popular' });
}
