/**
 * Grille produits responsive avec skeletons - OPTIMISÉE
 */
'use client';

import { OptimizedProductCard } from './OptimizedProductCard';
import { Product } from './ProductCard';

interface ProductGridProps {
  products: Product[];
  isLoading?: boolean;
}

export function ProductGrid({ products, isLoading }: ProductGridProps) {
  if (isLoading) {
    return (
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">
        {[...Array(8)].map((_, i) => (
          <div
            key={i}
            className="bg-white rounded-xl overflow-hidden h-full animate-pulse"
            aria-label="Chargement produit"
          >
            <div className="w-full h-80 bg-gray-200" />
            <div className="p-6 space-y-3">
              <div className="h-4 bg-gray-200 rounded w-3/4" />
              <div className="h-4 bg-gray-200 rounded w-1/2" />
              <div className="h-10 bg-gray-200 rounded" />
            </div>
          </div>
        ))}
      </div>
    );
  }

  if (products.length === 0) {
    return (
      <div className="text-center py-12">
        <p className="text-gray-500 text-lg" role="status">
          Aucun produit trouvé
        </p>
      </div>
    );
  }

  return (
    <div
      className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6"
      role="list"
      aria-label="Liste des produits"
    >
      {products.map((product, index) => (
        <div key={product.id} role="listitem">
          <OptimizedProductCard
            product={product}
            priority={index < 4} // Priorité pour les 4 premiers produits
          />
        </div>
      ))}
    </div>
  );
}
