/**
 * FilterSidebar accessible avec navigation clavier
 */
'use client';

import { useRouter, useSearchParams } from 'next/navigation';
import { useEffect, useRef, useState } from 'react';

interface AccessibleFilterSidebarProps {
  categories: string[];
}

export function AccessibleFilterSidebar({ categories }: AccessibleFilterSidebarProps) {
  const router = useRouter();
  const searchParams = useSearchParams();
  const firstCheckboxRef = useRef<HTMLInputElement>(null);

  const [selectedCategories, setSelectedCategories] = useState<string[]>(
    searchParams.get('category')?.split(',').filter(Boolean) || []
  );
  const [priceMin, setPriceMin] = useState(searchParams.get('price_min') || '');
  const [priceMax, setPriceMax] = useState(searchParams.get('price_max') || '');
  const [stockStatus, setStockStatus] = useState<string>(() => {
    const stockStatusParam = searchParams.get('stock_status');
    if (stockStatusParam) return stockStatusParam;
    if (searchParams.get('stock') === 'true') return 'in_stock';
    return 'any';
  });
  const [selectedTags, setSelectedTags] = useState<string[]>(
    searchParams.get('tags')?.split(',').filter(Boolean) || []
  );
  const [availableTags, setAvailableTags] = useState<string[]>([]);
  const [loadingTags, setLoadingTags] = useState(false);

  // Charger les tags disponibles
  useEffect(() => {
    const fetchTags = async () => {
      setLoadingTags(true);
      try {
        const API_URL = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:3001/api';
        const response = await fetch(`${API_URL}/products/tags`);
        if (response.ok) {
          const data = await response.json();
          setAvailableTags(data.tags || []);
        }
      } catch (error) {
        if (process.env.NODE_ENV === 'development') {
          console.error('Erreur chargement tags:', error);
        }
      } finally {
        setLoadingTags(false);
      }
    };

    fetchTags();
  }, []);

  // Focus management pour accessibilité
  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => {
      if (e.key === 'Escape') {
        firstCheckboxRef.current?.focus();
      }
    };

    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, []);

  // Appliquer filtres à l'URL
  useEffect(() => {
    const params = new URLSearchParams(searchParams.toString());

    // Reset pagination when filters change
    params.delete('page');

    if (selectedCategories.length > 0) {
      params.set('category', selectedCategories.join(','));
    } else {
      params.delete('category');
    }

    if (priceMin) {
      params.set('price_min', priceMin);
    } else {
      params.delete('price_min');
    }

    if (priceMax) {
      params.set('price_max', priceMax);
    } else {
      params.delete('price_max');
    }

    // Stock status (nouveau format)
    if (stockStatus && stockStatus !== 'any') {
      params.set('stock_status', stockStatus);
    } else {
      params.delete('stock_status');
    }
    // Supprimer l'ancien paramètre stock pour compatibilité
    params.delete('stock');

    // Tags
    if (selectedTags.length > 0) {
      params.set('tags', selectedTags.join(','));
    } else {
      params.delete('tags');
    }

    router.push(`/products?${params.toString()}`);
  }, [selectedCategories, priceMin, priceMax, stockStatus, selectedTags, router, searchParams]);

  const toggleCategory = (category: string) => {
    setSelectedCategories((prev) =>
      prev.includes(category)
        ? prev.filter((c) => c !== category)
        : [...prev, category]
    );
  };

  const toggleTag = (tag: string) => {
    setSelectedTags((prev) =>
      prev.includes(tag)
        ? prev.filter((t) => t !== tag)
        : [...prev, tag]
    );
  };

  const resetFilters = () => {
    setSelectedCategories([]);
    setPriceMin('');
    setPriceMax('');
    setStockStatus('any');
    setSelectedTags([]);
    firstCheckboxRef.current?.focus();
  };

  return (
    <aside
      className="w-full md:w-64 bg-white dark:bg-gray-800 p-6 rounded-lg shadow-sm border border-gray-200 dark:border-gray-700"
      aria-label="Filtres produits"
    >
      <div className="flex items-center justify-between mb-6">
        <h2 className="text-lg font-semibold" id="filters-heading">
          Filtres
        </h2>
        <button
          onClick={resetFilters}
          className="text-sm text-primary-600 hover:text-primary-700 focus:outline-none focus:ring-2 focus:ring-primary-500 rounded"
          aria-label="Réinitialiser tous les filtres"
        >
          Réinitialiser
        </button>
      </div>

      {/* Catégories */}
      <div className="mb-6">
        <h3 className="font-semibold mb-3" id="categories-heading">
          Catégories
        </h3>
        <div
          className="space-y-2"
          role="group"
          aria-labelledby="categories-heading"
        >
          {categories.map((category, index) => (
            <label
              key={category}
              className="flex items-center gap-2 cursor-pointer hover:text-primary-600 focus-within:text-primary-600"
            >
              <input
                ref={index === 0 ? firstCheckboxRef : undefined}
                type="checkbox"
                checked={selectedCategories.includes(category)}
                onChange={() => toggleCategory(category)}
                className="w-4 h-4 text-primary-600 rounded focus:ring-primary-500 focus:ring-2"
                aria-label={`Filtrer par catégorie ${category}`}
              />
              <span className="text-sm">{category}</span>
            </label>
          ))}
        </div>
      </div>

      {/* Prix */}
      <div className="mb-6">
        <h3 className="font-semibold mb-3" id="price-heading">
          Prix
        </h3>
        <div className="space-y-3" role="group" aria-labelledby="price-heading">
          <div>
            <label htmlFor="price-min" className="block text-sm text-gray-600 mb-1">
              Min (€)
            </label>
            <input
              id="price-min"
              type="number"
              value={priceMin}
              onChange={(e) => setPriceMin(e.target.value)}
              min="0"
              className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-primary-500"
              aria-label="Prix minimum en euros"
            />
          </div>
          <div>
            <label htmlFor="price-max" className="block text-sm text-gray-600 mb-1">
              Max (€)
            </label>
            <input
              id="price-max"
              type="number"
              value={priceMax}
              onChange={(e) => setPriceMax(e.target.value)}
              min="0"
              className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-primary-500"
              aria-label="Prix maximum en euros"
            />
          </div>
        </div>
      </div>

      {/* Disponibilité */}
      <div className="mb-6">
        <h3 className="font-semibold mb-3" id="availability-heading">
          Disponibilité
        </h3>
        <div className="space-y-2" role="group" aria-labelledby="availability-heading">
          <label className="flex items-center gap-2 cursor-pointer hover:text-primary-600">
            <input
              type="radio"
              name="stock_status"
              value="any"
              checked={stockStatus === 'any'}
              onChange={(e) => setStockStatus(e.target.value)}
              className="w-4 h-4 text-primary-600 focus:ring-primary-500 focus:ring-2"
              aria-label="Tous les produits"
            />
            <span className="text-sm">Tous</span>
          </label>
          <label className="flex items-center gap-2 cursor-pointer hover:text-primary-600">
            <input
              type="radio"
              name="stock_status"
              value="in_stock"
              checked={stockStatus === 'in_stock'}
              onChange={(e) => setStockStatus(e.target.value)}
              className="w-4 h-4 text-primary-600 focus:ring-primary-500 focus:ring-2"
              aria-label="En stock (plus de 5 unités)"
            />
            <span className="text-sm">En stock</span>
          </label>
          <label className="flex items-center gap-2 cursor-pointer hover:text-primary-600">
            <input
              type="radio"
              name="stock_status"
              value="low_stock"
              checked={stockStatus === 'low_stock'}
              onChange={(e) => setStockStatus(e.target.value)}
              className="w-4 h-4 text-primary-600 focus:ring-primary-500 focus:ring-2"
              aria-label="Stock faible (1-5 unités)"
            />
            <span className="text-sm">Stock faible (1-5)</span>
          </label>
          <label className="flex items-center gap-2 cursor-pointer hover:text-primary-600">
            <input
              type="radio"
              name="stock_status"
              value="out_of_stock"
              checked={stockStatus === 'out_of_stock'}
              onChange={(e) => setStockStatus(e.target.value)}
              className="w-4 h-4 text-primary-600 focus:ring-primary-500 focus:ring-2"
              aria-label="Rupture de stock"
            />
            <span className="text-sm">Rupture de stock</span>
          </label>
        </div>
      </div>

      {/* Tags */}
      {availableTags.length > 0 && (
        <div className="mb-6">
          <h3 className="font-semibold mb-3" id="tags-heading">
            Tags
          </h3>
          <div className="space-y-2 max-h-48 overflow-y-auto" role="group" aria-labelledby="tags-heading">
            {loadingTags ? (
              <p className="text-sm text-gray-500">Chargement...</p>
            ) : (
              availableTags.slice(0, 20).map((tag) => (
                <label
                  key={tag}
                  className="flex items-center gap-2 cursor-pointer hover:text-primary-600 focus-within:text-primary-600"
                >
                  <input
                    type="checkbox"
                    checked={selectedTags.includes(tag)}
                    onChange={() => toggleTag(tag)}
                    className="w-4 h-4 text-primary-600 rounded focus:ring-primary-500 focus:ring-2"
                    aria-label={`Filtrer par tag ${tag}`}
                  />
                  <span className="text-sm">{tag}</span>
                </label>
              ))
            )}
          </div>
        </div>
      )}
    </aside>
  );
}
